import styled from "styled-components";

export const SearchbarContainer = styled.div`
  display: flex;
  align-items: center;
  text-align: center;
  border: 1px solid #f1f1f2;
  border-radius: 5px;
  width: 90%;
  padding: 10px;
`;

export const Searchbar = styled.input`
  align-items: center;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  margin-left: 5px;
  font-size: 16px;
`;
