import React from "react";
import { SearchbarContainer, Searchbar } from "./location-searchbar.styles";
import { ReactComponent as PinIcon } from "../../assets/place-white-24dp.svg";

const LocationSearchbar = ({ placeholder, onChange }) => (
  <SearchbarContainer>
    <PinIcon fill="#f9423a"/>
    <Searchbar type="search" placeholder={placeholder} onChange={onChange} />
  </SearchbarContainer>
);

export default LocationSearchbar;
