import styled from "styled-components";

export const LocationListContainer = styled.div`
  display: flex;
  flex-direction: column;
  cursor: pointer;
  margin-top: 30px;
`;

export const LocationName = styled.h2`
  font-size: 16px;
  font-weight: 700;
  font-family: sans-serif;
  color: #424749;

  margin-top: 12px;
  margin-bottom: 0px;
`;

export const LocationAddress = styled.h4`
  font-size: 12px;
  font-weight: 400;
  font-family: sans-serif;
  color: #6e7679;

  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 310px;

  margin-top: 0px;
  margin-bottom: 0px;
  padding-bottom: 8px;
  border-bottom: 1px solid #f1f1f2;
`;

export const ListContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const PinContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(226, 228, 228, 0.3);
  border-radius: 100%;
  width: 30px;
  height: 30px;
  margin-right: 8px;
`;
