import React from "react";
import { ReactComponent as PinIcon } from "../../assets/place-white-24dp.svg";
import {
  LocationListContainer,
  LocationName,
  LocationAddress,
  ListContainer,
  PinContainer,
} from "./location-list.styles";

const LocationList = ({ locations }) => (
  <LocationListContainer>
    {locations.map((location) => (
      <ListContainer>
        <PinContainer>
          <PinIcon fill="#6e7679" />
        </PinContainer>
        <div>
          <LocationName> {location.name} </LocationName>
          <LocationAddress>{location.street}</LocationAddress>
        </div>
      </ListContainer>
    ))}
  </LocationListContainer>
);

export default LocationList;
