import React, { useState } from "react";
import Modal from "react-responsive-modal";
import LocationList from "../location-list/location-list.component";
import LocationSearchbar from "../location-searchbar/location-searchbar.component";
  

import "react-responsive-modal/styles.css";
import "./location-modal.styles.css";

import {
  LocationCard,
  AlamatPengantaran,
  Destination,
  ArrowIcon,
  DownArrowIcon,
  DestinationContainer,
  ModalText,
} from "./location-container.styles";

const LocationContainer = () => {
  //Modal State
  const [open, setOpen] = useState(false);
  //Search Functionality State
  const [searchField, setSearchField] = useState("");
  const [locations] = useState([
    {
      name: "Tokopedia Tower",
      street: "Jl. Prof. DR. Satrio No.Kav. 11, RT.3/RW.3, Karet Semanggi, Kota Jakarta Selatan, Daerah Khusus Ibukota Jakarta 12950",
    },
    {
      name: "Kulina Tower",
      street: "Jl. Tulodong Atas No.28, RT.6/RW.3, Senayan, Kec. Kby. Baru, Kota Jakarta Selatan, Daerah Khusus Ibukota Jakarta 12190",
    },
    {
      name: "Fakultas Teknik Universitas Gadjah Mada",
      street: "Universitas Gadjah Mada, Jl. Grafika No.2, Senolowo, Sinduadi, Mlati, Sleman Regency, Special Region of Yogyakarta 55284",
    },
    {
      name: "Hartono Mall Yogyakarta",
      street: "Jl. Ring Road Utara, Kaliwaru, Condongcatur, Kec. Depok, Kabupaten Sleman, Daerah Istimewa Yogyakarta 55281",
    },
  ]);

  const filteredLocations = locations.filter(location => 
      location.name.toLowerCase().includes(searchField.toLowerCase())
    )

  return (
    <div>
      <LocationCard>
        <ArrowIcon>&#8592;</ArrowIcon>
        <div>
          <AlamatPengantaran>ALAMAT PENGANTARAN</AlamatPengantaran>
          <DestinationContainer>
            <Destination>Tokopedia Tower</Destination>
            <DownArrowIcon onClick={() => setOpen(true)}>&#8964;</DownArrowIcon>
            <Modal
              open={open}
              onClose={() => setOpen(false)}
              classNames={{
                overlay: "customOverlay",
                modal: "customModal",
              }}
            >
              <ModalText>Cek makanan yang tersedia di lokasi kamu!</ModalText>
              <LocationSearchbar placeholder="Search Location" onChange={e => setSearchField(e.target.value)}/>
              <LocationList locations={filteredLocations} />
            </Modal>
          </DestinationContainer>
        </div>
      </LocationCard>
    </div>
  );
};

export default LocationContainer;
